/* Premium About Page */
.about-page {
  width: 100%;
  background: transparent;
}

/* About Hero - Premium Style */
.about-hero {
  position: relative;
  min-height: 65vh;
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.about-hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(7, 12, 13, 0.9) 0%, rgba(11, 17, 18, 0.82) 56%, rgba(14, 162, 115, 0.26) 100%);
}

.about-hero-content {
  position: relative;
  z-index: 1;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.about-hero-content h1 {
  color: var(--text-primary);
  margin-bottom: 24px;
  text-shadow: none;
}

.about-hero-content p {
  color: var(--text-secondary);
  font-size: 20px;
}

/* Story Section - Premium */
.story-section {
  padding: 120px 0;
  background: transparent;
  position: relative;
}

.story-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--brand-green), transparent);
}

.story-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
}

.story-text h2 {
  color: var(--brand-green-dark);
  margin-bottom: 32px;
  position: relative;
  display: inline-block;
}

.story-text h2::after {
  content: '';
  position: absolute;
  bottom: -12px;
  left: 0;
  width: 72px;
  height: 2px;
  background: var(--brand-green-dark);
  border-radius: 999px;
  opacity: 0.6;
}

.story-text p {
  margin-bottom: 24px;
  line-height: 1.8;
  font-size: 17px;
}

.story-facts {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.story-fact {
  padding: 20px 24px;
  border-radius: 14px;
  border: 1px solid var(--card-border);
  background: var(--card-bg);
}

.story-fact__title {
  color: var(--text-primary);
  font-weight: 700;
  margin-bottom: 8px;
}

.story-fact__desc {
  color: var(--text-secondary);
  line-height: 1.6;
}

.story-image {
  width: 100%;
  height: 550px;
  background-size: cover;
  background-position: center;
  border-radius: 18px;
  border: 1px solid var(--card-border);
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.45);
  position: relative;
  overflow: hidden;
}

.story-image::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--brand-green);
  opacity: 0.8;
}

/* Story Video */
.story-video {
  width: 100%;
  height: 550px;
  border-radius: 18px;
  border: 1px solid var(--card-border);
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.45);
  position: relative;
  overflow: hidden;
}

.story-video::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--brand-green);
  opacity: 0.8;
  z-index: 1;
}

.story-video-player {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Reality Section */
.reality-section {
  padding: 120px 0;
  background: transparent;
  position: relative;
}

.reality-layout {
  display: grid;
  grid-template-columns: 1fr minmax(320px, 360px);
  gap: 72px;
  align-items: center;
}

.reality-video-column {
  display: flex;
  justify-content: center;
}

.reality-video-frame {
  width: 100%;
  max-width: 360px;
  aspect-ratio: 9 / 16;
  border-radius: 18px;
  overflow: hidden;
  border: 1px solid rgba(36, 203, 143, 0.16);
  box-shadow: 0 18px 44px rgba(0, 0, 0, 0.55), 0 0 0 1px rgba(36, 203, 143, 0.08);
  background: radial-gradient(circle at 30% 20%, rgba(255, 255, 255, 0.04), transparent 38%),
    radial-gradient(circle at 75% 0%, rgba(14, 162, 115, 0.18), transparent 42%), #080b0a;
  position: relative;
}

.reality-video-surface {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 16px;
  background: linear-gradient(180deg, rgba(6, 9, 8, 0.75), rgba(6, 9, 8, 0.95));
  isolation: isolate;
  cursor: pointer;
}

.reality-video-frame::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, transparent, var(--brand-green), transparent);
  opacity: 0.9;
  z-index: 1;
}

.reality-video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  background-color: #0a0a0a;
}

.reality-preview {
  position: absolute;
  inset: 0;
  border: none;
  background: linear-gradient(180deg, rgba(6, 9, 8, 0.75), rgba(6, 9, 8, 0.92));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 22px;
  cursor: pointer;
  padding: 18px;
  color: var(--text-primary);
  z-index: 2;
  transition: background 180ms ease;
}

.reality-preview:hover {
  background: linear-gradient(180deg, rgba(8, 13, 11, 0.82), rgba(8, 13, 11, 0.98));
}

.reality-preview-logo {
  display: grid;
  place-items: center;
  width: 120px;
  height: 64px;
  background: rgba(18, 26, 23, 0.8);
  border: 1px solid rgba(36, 203, 143, 0.26);
  border-radius: 12px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(10px);
}

.reality-preview-logo img {
  max-width: 100%;
  max-height: 44px;
  object-fit: contain;
}

.reality-preview-play {
  width: 86px;
  height: 86px;
  border-radius: 50%;
  background: linear-gradient(145deg, #24cb8f, #1fa172);
  box-shadow: 0 16px 28px rgba(36, 203, 143, 0.4), inset 0 0 0 10px rgba(255, 255, 255, 0.03);
  display: grid;
  place-items: center;
  color: #0a0f0d;
  transition: transform 160ms ease;
}

.reality-preview:hover .reality-preview-play {
  transform: translateY(-2px);
}

.reality-mute-toggle {
  position: absolute;
  right: 14px;
  bottom: 14px;
  width: 42px;
  height: 42px;
  border-radius: 14px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background: rgba(7, 10, 9, 0.75);
  color: #e8f7f0;
  display: grid;
  place-items: center;
  cursor: pointer;
  z-index: 2;
  transition: background 160ms ease, border-color 160ms ease;
}

.reality-mute-toggle:hover {
  background: rgba(10, 15, 13, 0.9);
  border-color: rgba(36, 203, 143, 0.45);
}

.reality-text {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.reality-text .section-header {
  align-items: flex-start;
  gap: 6px;
}

.reality-lead {
  color: var(--text-secondary);
  margin-top: 8px;
  margin-bottom: 12px;
  line-height: 1.7;
}

.reality-list {
  list-style: none;
  padding: 0;
  margin: 28px 0 0;
  display: grid;
  grid-template-columns: repeat(2, minmax(260px, 1fr));
  gap: 16px 28px;
}

.reality-list-item {
  display: flex;
  align-items: flex-start;
  gap: 14px;
  color: var(--text-primary);
  font-weight: 600;
  line-height: 1.5;
}

.reality-icon {
  width: 42px;
  height: 42px;
  min-width: 42px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: rgba(255, 255, 255, 0.04);
  display: grid;
  place-items: center;
  color: var(--brand-green);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.04);
}

/* Values Section - Premium Cards */
.values-section {
  padding: 120px 0;
  background: transparent;
}

.section-title {
  text-align: center;
  margin-bottom: 80px;
  color: var(--text-primary);
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 2px;
  background: var(--brand-green-dark);
  border-radius: 999px;
  opacity: 0.6;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
}

.value-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  padding: 48px 32px;
  text-align: center;
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
}

.value-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--brand-green);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.value-card:hover {
  transform: translateY(-3px);
  border-color: var(--brand-green);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.45);
}

.value-card:hover::before {
  opacity: 1;
}

.value-icon {
  width: 88px;
  height: 88px;
  margin: 0 auto 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.04);
  color: var(--brand-green);
  border-radius: 16px;
  transition: all 0.25s ease;
  border: 1.5px solid var(--card-border);
  box-shadow: none;
}

.value-card:hover .value-icon {
  background: var(--brand-green);
  color: #0A0A0A;
  transform: scale(1.03);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.35);
}

.value-card h3 {
  color: var(--text-primary);
  margin-bottom: 16px;
  font-size: 20px;
}

.value-card p {
  color: var(--text-secondary);
  line-height: 1.7;
}

/* Production Details - Premium Stats */
.production-details-section {
  padding: 120px 0;
  background: transparent;
  position: relative;
}

.production-details-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--brand-green), transparent);
}

.production-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}

.production-item {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  padding: 56px 32px;
  text-align: center;
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: none;
}

.production-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--brand-green);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.production-item:hover {
  border-color: var(--brand-green);
  transform: translateY(-3px);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.45);
}

.production-item:hover::before {
  transform: scaleX(1);
}

.production-number {
  font-size: 56px;
  font-weight: 800;
  color: var(--brand-green);
  margin-bottom: 20px;
  line-height: 1;
  text-shadow: none;
}

.production-label {
  color: var(--text-secondary);
  font-size: 16px;
  line-height: 1.6;
}

/* Mission Section - Premium Quote */
.mission-section {
  padding: 80px 0;
  background: transparent;
  position: relative;
}

.mission-content {
  max-width: 820px;
  margin: 0 auto;
  text-align: left;
  padding: 48px 40px;
  background: var(--card-bg);
  border-radius: 18px;
  border: 1px solid var(--card-border);
  position: relative;
  overflow: hidden;
  backdrop-filter: none;
}

.mission-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, transparent, var(--brand-green), transparent);
}

.mission-content h2 {
  color: var(--text-primary);
  margin-bottom: 12px;
}

.mission-content .section-header {
  margin-bottom: 12px;
}

.mission-content p {
  margin: 0;
  font-size: 16px;
  line-height: 1.6;
}

/* Mobile Responsive */
@media (max-width: 1024px) {
  .story-content {
    grid-template-columns: 1fr;
    gap: 60px;
  }

  .reality-section {
    padding: 100px 0;
  }

  .reality-layout {
    grid-template-columns: 1fr;
    gap: 48px;
  }

  .reality-video-frame {
    max-width: 420px;
    margin: 0 auto;
  }

  .reality-list {
    grid-template-columns: 1fr;
  }
  
  .story-image {
    height: 450px;
  }
  
  .values-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .production-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .about-hero {
    min-height: 55vh;
    background-attachment: scroll;
  }
  
  .about-hero-content p {
    font-size: 17px;
  }
  
  .story-section,
  .values-section,
  .production-details-section,
  .mission-section {
    padding: 100px 0;
  }

  .mission-section {
    padding: 80px 0;
  }
  
  .section-title {
    margin-bottom: 60px;
  }
  
  .story-image {
    height: 350px;
  }
  
  .values-grid,
  .production-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .mission-content {
    padding: 40px 28px;
  }

  .mission-content p {
    font-size: 15px;
  }
}
